body {
  margin: 0;
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 200;
  line-height: 1.3;
  color: #000;
  text-align: left;
  background-color: #fff;
}

img {
  vertical-align: middle;
  border-style: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 100%;
  padding: 20px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

.container .row {
  margin-left: -20px;
  margin-right: -20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
  outline: none;
}

.form-group {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 400px;
  padding: 20px;
}

.form-group .custom-select {
  background-color: #eaeaea;
  background-image: url("/images/arrow-icon.png?v=1");
  background-repeat: no-repeat;
  background-position: 95% center;
}

.form-group .form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 200;
  line-height: 1.5;
  color: #000;
  background-color: transparent;
  background-clip: padding-box;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-group select.form-control:not([size]):not([multiple]) {
  height: calc(2.25rem + 2px);
}

#player-profile {
  width: 100%;
  background: url("../images/background.png?v=1") top right no-repeat;
  opacity: 0;
  transition: opacity 0.4s ease-in;
}

#player-profile.show {
  opacity: 1;
}

#player-profile .player__img {
  padding: 0 8px;
}

#player-profile .player__info {
  background: #ea004e;
  position: relative;
  padding: 20px;
  color: #fff;
}

#player-profile .player__info .player__name {
  font-size: 1.5rem;
  margin-bottom: 0.4rem;
}

#player-profile .player__info .player__club {
  position: absolute;
  right: 20px;
  top: -50px;
  border: 5px solid white;
  border-radius: 100%;
  background: #fff;
}

#player-profile .player__info .player__club span {
  border-radius: 100%;
  display: block;
  height: 100px;
  width: 100px;
}

#player-profile .player__info .player__club .manchester-united {
  background-image: url("../images/badges_sprite.png?v=1");
  background-position: 600px 300px;
}

#player-profile .player__info .player__club .manchester-city {
  background-image: url("../images/badges_sprite.png?v=1");
  background-position: 400px 400px;
}

#player-profile .player__info .player__club .tottenham-hotspur {
  background-image: url("../images/badges_sprite.png?v=1");
  background-position: 700px 1200px;
}

#player-profile .player__info .player__club .arsenal {
  background-image: url("../images/badges_sprite.png?v=1");
  background-position: 1100px 1000px;
}

#player-profile .player__info .player__club .leicester-city {
  background-image: url("../images/badges_sprite.png?v=1");
  background-position: 0px 0px;
}

#player-profile .player__stats {
  margin-top: 20px;
  color: #38003c;
}

#player-profile .player__stats span {
  font-size: 1.1rem;
  float: right;
  font-weight: bold;
}

.list-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
}

.list-group .list-group-item {
  position: relative;
  display: block;
  padding: 0.75rem 1rem;
  margin-bottom: 2px;
  background-color: #fff;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.2);
}

.list-group .list-group-item:last-child {
  margin-bottom: 0;
}

